.App {
  text-align: center;
  font-family: 'Inter', sans-serif;
}
:root {
  --max-width: 1000px;
  --footer-height: 118px;
}

@media (max-width: 370px) {
  .sidebar .profile-img {
    display: none;
  }

  .wrapper .sidebar .profile-content {
    padding-left: 0px ;
  }

  .wrapper .body .sections ul li {
    display: block;
    padding-right: 0px;
  }

  .wrapper .body .body-content {
    min-height: calc(87vh - 118px);
    min-height: calc(87vh - var(--footer-height));
  }
}

@media (max-width: 1020px) {
  :root {
    --max-width: 750px;
  }

  .sidebar .profile-img img {
    max-width: calc(1000px * 0.15);
    max-width: calc(var(--max-width) * 0.15);
  }

  .wrapper .body {
    width: calc(0.72 * 1000px);
    width: calc(0.72 * var(--max-width))
  }
}

@media (max-width: 768px) {

  .sidebar .profile-content ul {
    text-align: left;
  }

  #main .mobile-divider {
    display: inherit;
  }

  .body .sections {
    display: none;
  }

  .sidebar .dd {
    display: inherit;
  }

  .sidebar .dd {
    margin-left: auto;
  }

  .sidebar .dd .dd-button {
    display: inherit;
    text-align: right;
  }

  .sidebar .dd .dd-menu {
    display: block;
    position: absolute;
    float: right;
    right: 5px;
    top: 80px;
    background-color: #f1f1f1;
    z-index: 100;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2)
  }

  .dd-menu a:hover{
    background-color: #ddd;
  }

  .dd-menu a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
  }

  .wrapper .sidebar {
    display: flex;
    width: auto;
    height: auto;
    position: relative;
    top: 0px;
    padding: 15px 5px 5px 0;
  }

  .sidebar .profile-img img {
    max-height: 100px;
  }

  .sidebar .profile-img {
    padding-left: 0;
  }

  .wrapper .body {
    width: 100% ;
  }

  .body .sections {
    width: 100%  ;
    position: relative;
    padding-top: 0;
    background-color: #1b1b1b;
  } 

  .sidebar .profile-content {
    padding-left: 15px;
    padding-top: 20px;
    display: table-cell;
  }
  
  .profile-content .profile-name {
    margin-bottom: 3px;
  }
  
  .profile-content .profile-text {
    margin-bottom: 0;
  }

  #main .wrapper {
    display: block;
  }

  .body .body-content {
    padding-top: 0;
    min-height: calc(84.2vh - 118px);
    min-height: calc(84.2vh - var(--footer-height));
  }
}

.dd-button {
  padding-top: 20px;
}

.dd {
  display: none;
}

.mobile-divider {
  display: none;
}

#main {
    margin-left: auto;
    margin-right: auto;
    max-width: 1000px;
    max-width: var(--max-width);
  }

  .body {
    width: calc(0.72 * 1000px);
    width: calc(0.72 * var(--max-width));
    text-align: left;
    overflow: auto;
  }
  
  .sidebar {
    width: calc(0.2 * 1000px);
    width: calc(0.2 * var(--max-width));
    height: 235px;
    position: -webkit-sticky;
    position: sticky;
    top: 70px;
  }

  .wrapper {
    display: flex;
    justify-content: space-between;
    margin: 0 20px;
  }
  
  .profile-img {
    padding: 5px;
    display: inherit;
  }

  .profile-img img {
      max-width: 175px;
      border-radius: 50%;
  }

  .profile-content {
      text-align: left;
      padding: 10px 5px 5px 5px;
      display: inherit;
      vertical-align: middle;
  }

  .profile-name {
      font-size: 1rem;
  }

  .profile-text {
      font-size: 0.75rem;
      margin-bottom: 0.5rem;
  }
  
.sections {
    position: fixed;
    z-index: 10;
    padding-top: 75px;
    width: calc(0.72 * 1000px);
    width: calc(0.72 * var(--max-width));
    background-color: white;
}

.body-content {
    min-height: calc(100vh - 118px);
    min-height: calc(100vh - var(--footer-height));
    padding-top:133px;
    z-index:5;
    padding-bottom: 15px;
}

.sections ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.sections li {
  display: inline;
  padding-right: 25px;
}

.sections li a {
    text-align: center;
    text-decoration: none;
    text-decoration: underline;
}

.profile-content .fa-icon {
  color: #1b1b1b;
}

.profile-content ul {
  text-align: center;
  list-style: inside;
  padding-left: 0;
}

.profile-content ul > li {
  display: inline-block;
  padding-right: 10px;
  padding-left: 10px;
}

.bio-divider {
  margin: 0.7rem 0;
}
#copyright {
    color: #bcbcbc;
    text-align: center;
    font-size: .9rem;
  }

  #footer {
    background: #1b1b1b;
    padding: 20px 0;
    min-height: 118px;
  }
  
  #footer ul > li {
    display: inline-block;
    padding-right: 10px;
    padding-left: 10px;
  }
  
  #footer ul {
    padding: 0;
  }
  
  .fa-icon:hover {
    color: grey;
  }
  
  .fa-icon {
    color: white
  }
.project {
    display: flex;
}

.project-section ul {
    list-style: none;
}

.project-section .project-description {
    display: block;
    padding-right: 50px;
}

.project-section .project-media  {
    display: inherit;
    margin-left: auto;
}

.project-media img  {
    width: 200px;
    height: 200px;
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}

@media (max-width: 768px) {
    .project-section .project {
        display: flex;
    }

    .project .project-description {
        padding-right: 0px;
        width: 350px;
    }
}

@media (max-width: 640px) {
    .project-section .project {
        display: flex;
        flex-direction: column-reverse;
    }

    .project-media img  {
        width: 60vw;
        max-width: 300px;
        height: auto;
        display: block;
        margin: auto;
        position: inherit;
        top: inherit;
        -webkit-transform: inherit;
                transform: inherit;
    }

    .project-section .project-media {
        margin-left: inherit;
        padding: 10px 0;
    }

    .project .project-description {
        padding: 10px 0 0 0;
        width: auto;
    }

    .project-media video  {
        display: block;
        margin: auto;
    }
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
